@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
}

#root {
  height: 100vh; /* ทำให้แน่ใจว่า body และ html ครอบคลุมเต็มหน้าจอ */
  width: 100%;
  display: flex;
  justify-content: center; /* จัดกึ่งกลางในแนวนอน */
  align-items: center; 
  background-color: #f0f2f5;
}

.card {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;    /* จัดกึ่งกลางในแนวตั้ง */
  background-color: white;
  width: 100%;
  max-width: 380px;
  height: 600px;
  color: black;
  border-radius: 8px;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

i {
  font-size: 2rem;
  color: rgb(132, 235, 235);
}

h1{
  font-weight: 700;
  color: #333;
  margin-top: 20px;
}

.currency_exchange {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60%;
}

.input_container {
  display: flex;
  flex-direction: column;
  width: 90%;
  margin-bottom: 10px;
}

.input_label {
  color: #8B8E98;
  font-weight: 600;
  font-size: 1.2rem;
}

.input_field {
  margin-top: 10px;
  background: #fff;
  padding: 0 0 0 40px;
  border-radius: 7px;
  outline: none;
  border: 1px solid #e5e5e5;
  height: 60px;
  font-size: 1.2rem;
  filter: drop-shadow(0px 1px 0px #efefef)
    drop-shadow(0px 1px 0.5px rgba(239, 239, 239, 0.5));
  transition: all 0.3s cubic-bezier(0.15, 0.83, 0.66, 1);
}

.input_field:focus {
  border: 1px solid transparent;
  background: #fff;
  box-shadow: 0px 0px 0px 2px #242424;
  background-color: transparent;
}

.output {
  width: 90%;
  background-color: rgba(132, 235, 235, 0.219);
  margin-bottom: 20px;
  padding: 25px;
  border-radius: 10px;
}

h2 {
  text-align: start;
  border-radius: 10px;
  font-weight: 500;
  font-size: 1.1rem;
}

@media (min-width: 745px) {
  .card {
    display: flex;
    justify-content: center;
    align-items: start;
    width: 90%;
    max-width: 700px;
    height: auto;
    padding: 20px;
  }

  .header {
    align-items: start;
    width: 100%;
    margin-bottom: 30px;
  }

  .currency_exchange {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    height: auto;
    margin-bottom: 20px;
  }
  
  .input_container {
    display: flex;
    flex-direction: column;
    width: 200px;
    margin-right: 10px;
  }

  .input_label {
    font-size: 0.8rem;
  }

  .input_field {
    margin-top: 10px;
    padding: 0 0 0 40px;
    height: 60px;
    font-size: 1.2rem;
  }

  .output {
    width: 50%;
  }
}
